.footer {
	background-color: #041827;
	// margin-top: -21px;
	margin-top: auto;
}

.footer__wrapper {
	display: flex;
	align-items: flex-start;
	padding-top: 50px;
	padding-bottom: 45px;
}

.footer__additional {
	display: flex;
	flex-direction: column;
	max-width: 215px;
	margin-right: 160px;
}

.footer__link {
	width: max-content;
	margin-bottom: 30px;
}

@media (max-width: 1919px) {
	.footer__wrapper {
		flex-wrap: wrap;
		justify-content: space-between;
		max-width: none;
		padding-bottom: 63px;
	}

	.footer__additional {
		flex-direction: row;
		justify-content: space-between;
		align-items: center;
		max-width: none;
		width: 100%;
		margin: 0;
		margin-bottom: 26px;
	}

	.footer__link {
		margin: 0;
	}
}

@media (max-width: 769px) {
	.footer {
		margin-top: 0;
	}

	.footer__wrapper {
		padding-top: 35px;
		padding-bottom: 35px;
	}

	.footer__additional {
		flex-direction: column;
		margin-bottom: 25px;
	}

	.footer__link {
		margin-bottom: 16px;
	}
}